export class ParamModel {

    /**
     * 参数名
     */
    paramName: string;

    /**
     * 参数键
     */
    paramKey: string;
    
    /**
     * 参数值
     */
    paramValue: any;
    
    /**
     * 备注
     */
    remark: string;
    
    /**
     * 是否显示该参数
     */
    isVisible: string;

}


